import logo from './logo.svg';
import './App.css';
import GooglePayButton from "@google-pay/button-react"
import QRCode from "react-qr-code";
import { useEffect, useState } from 'react';
function App() {

 

  const [merchant, setMerchant] = useState("");
  const [vpa, setVpa] = useState("");
  const [amount, setAmount] = useState("");
  const [notes, setNotes] = useState("");


  const api = `upi://pay?pa=${vpa}&pn=${merchant + ""}&am=${amount}&cu=${"INR"}&tn=${notes + ""}&`;

  const qrCodeUrlParams = [
    "format=png",
    "size=1000x1000",
    "data=" + encodeURIComponent(api),
  ];
  const qrCodeUrl = "https://api.qrserver.com/v1/create-qr-code/?" + qrCodeUrlParams.join("&");





  return (
    <div className="App">
      <center>
        <br />
        <br />
        <input
          type="text"
          onChange={(e) => setMerchant(e.target.value)}
          placeholder="Merchant-Name"
        />
        <br />
        <br />
        <input
          type="text"
          onChange={(e) => setVpa(e.target.value)}
          placeholder="Upi-Id"
        />
        <br />
        <br />
        <input
          type="number"
          onChange={(e) => setAmount(e.target.value)}
          placeholder="Enter Your Amount"
        />
        <br />
        <br />
        <input
          type="text"
          onChange={(e) => setNotes(e.target.value)}
          placeholder="Notes"
        />
        <br />
        <br />
        <br />
        {/* {value && (
          <QRCode
            title="qrGENERATOR"
            value={qrCodeUrl}
            bgColor={back}
            fgColor={fore}
            size={size === '' ? 0 : size}
          />
        )} */}
      </center>
      <img src={qrCodeUrl} height="250px"></img>
    </div>
  );
}

export default App;
